                <h2 class = "login-head">
                    Account Log in
                </h2>
                <input type="text" class="login-input" v-model="username" placeholder="Username">
                <input type="text" class="login-input" v-model="password" placeholder="Password">
                <div class="login-btn">
                    <button class="login-btn-button" @click="tryLogin">
                        Log in
                    </button>
                </div>
                <div class="login-other">
                    <p class="login-other-f">Forgot passwod?</p>
                    <router-link
                        :to="{name:'register'}">
                        <p class="login-other-f">Register Now</p>
                    </router-link>
                </div>
                <div class="login-with">
                    <div class="login-with-img">
                        <button @click="apiLogin('google')"> 
                            <img src="../assets/google.png" alt="Google">
                        </button>
                    </div>
                    <div class="login-with-img">
                        <button @click="apiLogin('facebook')"> 
                            <img src="../assets/facebook.png" alt="Facebook">
                        </button>
                    </div>
                    <!--REMOVE DISCORD-->
                    <div class="login-with-img">
                        <img src="../assets/ds.png" alt="Discord">
                    </div>
                </div>
            </div>
        </div>
    </div>
    <PreLoader :isLoading = "load" />
    <Notification :message = "msg"/>
</template>

<script>

import PreLoader from '../components/PreLoader.vue'
import Notification from '../components/Notification.vue'
export default {
  name: 'LoginC',
  components: {
    PreLoader,
    Notification
  },
  data(){
        return{
            load:false,
            username:"",
            password:"",
            msg:"",
        }
  },
  methods: {
    async tryLogin() {
      try {
        this.load = true;
        const response = await fetch('https://syncme-server-a6c96ce1c319.herokuapp.com/auth/login', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(
            { 
                username: this.username, 
                password: this.password 
            }
        )
        });
        let res = await response.json();
        if(res.token){
            localStorage.setItem("loginToken", res.token);
            localStorage.setItem("name", this.username);
            this.$store.dispatch('getName',this.username)
            this.$emit('reg',res.token)
            this.$router.push({name:'main'})
        }
      } catch (error) {
        this.load = false;
        this.msg = ""
        this.msg = "Введено неправильні дані"
      }
    },
    async apiLogin(name) {
        window.location.href = `https://syncme-server-a6c96ce1c319.herokuapp.com/auth/${name}`
        this.$router.push({name:'main'})
    }
  }
}
</script>